$(".burger").click(function () {
    $("body").toggleClass("_open-menu")
    $("body").toggleClass("fixed-body")
})

$(".sidebar-user__show").click(function () {
    $(this).parents(".sidebar-user").toggleClass("_open")
})


$(document).mouseup( function(e){
    var div = $( ".sidebar-user,.header-contacts,.favourite-nav" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});
