let artists = [
    {id: 1, name: 'Морген'},
    {id: 2, name:'Второй'},
    {id: 3, name: 'Третий'},
    {id: 4, name: 'Четвертый'},
]

let artistNickname = document.querySelector("#artistNickname input")
let artistsSelect = document.querySelector("#artists input")

if(artistNickname){
    addTokenInput(artistNickname,artists)
}
if(artistsSelect){
    addTokenInput(artistsSelect,artists)
}



function addTokenInput(el,items) {
    const basic = new Tokenfield({
        el: el,
        items: items,
        addItemOnBlur: true,
        addItemsOnPaste: true,
        minChars: 0,
        maxSuggestWindow: 5,
        singleInput: true,
        singleInputValue: 'name',
        newItems: true,
    });

    basic.on('addedToken', function() {
        basic.el.classList.add("_added-tag")
    });
    basic.on('removeToken', function() {
       if(basic.getItems().length === 1){
           basic.el.classList.remove("_added-tag")
       }
    });
}


