$('.file-input input').change(function(){
    const file = this.files[0];

    if($(this).parents(".file-input").hasClass("_music-input")){
        console.log(file)
        $(this).parents(".file-input").find(".file-input__name").html(file.name)
        $(this).parents(".file-input").addClass("_uploaded")
        return
    }
    let self = $(this)
    if (file){
        let reader = new FileReader();
        reader.onload = function(event){
            $(self).parents(".file-input").find(".file-input__preview img").attr('src', event.target.result);
            $(self).parents(".file-input").addClass("_uploaded")
        }
        reader.readAsDataURL(file);
    }
});

let fileInput = document.querySelector("[data-upload-id='file-input']")

if(fileInput){
    const upload = new FileUploadWithPreview.FileUploadWithPreview('file-input');

    // upload.options.multiple = true
    console.log(upload)

    upload.options.accept = 'image/png, image/gif, image/jpeg'
    console.log(upload)

    $(document).on('change', '.input-container input', function(e) {
        $(this).parents(".file-input").addClass("_uploaded")
    })

    $('.input-container input').attr("accept",'image/png, image/gif, image/jpeg')

    $(document).on('click', '.image-preview-item-clear', function(e) {
        let count = $(this).parents(".file-input").find(".image-preview-item").length
        if(count === 1){
            $(this).parents(".file-input").removeClass("_uploaded")
        }
    })
}
